@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
html {
  scroll-behavior: smooth;
}
ul {
  list-style: none;
}
a {
  text-decoration: none;
}
hr {
  width: 60%;
  height: 2px;
  border: none;
  background-color: #ff0095;
  background-color: var(--primary-color);
  margin: 10px auto;
  border-radius: 10px;
}
:root {
  --primary-color: #ff0095;
  --secondary-color: #00fff2;
  --white-color: #ffffff;
  --black-color: rgb(0, 0, 0);
  --blue-white-color: #f3f3ff;
  --gry-color: rgb(29, 29, 29);

  /* gridient color */

  --gradient-color: var(--primary-color), var(--secondary-color);

  /* heights */
  --navbar-height: 100px;
}
body {
  background-color: #f3f3ff;
  background-color: var(--blue-white-color);
}

.navbar {
  width: 100%;
  height: var(--navbar-height);
  display: flex;
  align-items: center;
  justify-content: space-around;
  z-index: 1000;
  transition: 1s;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}
.navbar.activeScroll {
  background-color: var(--blue-white-color);
  box-shadow: 0 0 16px #00000050;
}
.navbar_logo {
  color: var(--white-color);
}
.activeScroll .navbar_logo {
  color: var(--black-color);
}
.nav_links {
  color: var(--white-color);
}
.activeScroll .navbar_container .navbar_links_container li .nav_links {
  color: var(--black-color);
}
.navbar_container .navbar_links_container li {
  display: inline-block;
  margin: 10px;
}
.menu_btn {
  display: none;
}
/* media query */
@media (max-width: 700px) {
  .navbar_container {
    position: fixed;
    top: 0;
    right: -80%;
    width: 80%;
    height: 100vh;
    background: var(--white-color);
    z-index: 100;
    transition: 0.5s;
  }
  .navbar_container.active {
    right: 0;
  }
  .navbar_container .navbar_links_container {
    padding: 20px 0 0 20px;
  }
  .navbar_container .navbar_links_container li {
    display: block;
  }
  .navbar_container .navbar_links_container li a {
    color: var(--black-color);
  }
  .menu_btn {
    display: block;
    color: var(--white-color);
    font-size: 30px;
    transition: 0.5s;
    cursor: pointer;
    z-index: 200;
  }
  .menu_btn.active {
    color: var(--black-color);
  }
  .activeScroll .menu_btn {
    color: var(--black-color);
  }
}

.Cards {
  position: relative;
  width: 250px;
  height: 350px;
  background-color: var(--white-color);
  box-shadow: 2px 8px 16px rgb(0 0 0/10%);
  border-radius: 20px;
  margin: 20px;
  overflow: hidden;
}
.cards_inset_one {
  position: absolute;
  inset: 0;
  bottom: 50%;
}
.cards_Images {
  width: 100%;
}
.cards_inset_second {
  position: absolute;
  inset: 0;
  top: 50%;
  padding: 10px;
  text-align: center;
  display: grid;
  grid-gap: 2px;
}
.cards_inset_second a {
  padding: 10px 30px;
  background-color: var(--primary-color);
  color: var(--white-color);
  justify-self: center;
  border-radius: 20px;
  transition: 0.5s;
}
.cards_inset_second a:hover {
  background-color: transparent;
  color: var(--black-color);
  box-shadow: 0 3px 16px rgb(0 0 0/30%);
}
.cards_inset_second a:active {
  box-shadow: none;
}

.CardsContainer {
  width: 95%;
  position: relative;
  display: flex;
  margin: auto;
  flex-wrap: wrap;
  justify-content: center;
}

.NewCards {
  position: relative;
  width: 350px;
  height: 500px;
  border-radius: 20px;
  box-shadow: 0 8px 16px rgb(0 0 0/20%);
  margin: 20px;
}
.topContent {
  position: absolute;
  inset: 0;
  bottom: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.center_logo {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  box-shadow: 0 2px 16px rgb(0 0 0/10%);
  overflow: hidden;
}
.center_logo img {
  width: 100%;
  height: 100%;
}
.bottomContent {
  text-align: center;
  padding: 20px;
  position: absolute;
  inset: 0;
  top: 50%;
}

.hero_section {
  width: 100%;
  height: 100vh;
  display: grid;
  place-items: center;
}
.image_container {
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  bottom: 0;
  background: linear-gradient(36deg, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.9)),
    url(/https//thejaved.github.io/reactwebsite01/static/media/hero_bg.eaf89d00.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  z-index: -1;
  overflow: hidden;
  border-radius: 0 0 300px 300px;
}
.conataner_first {
  width: 90%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: var(--white-color);
  grid-gap: 40px;
  margin-bottom: 10%;
  text-align: center;
}
.btn_container a {
  padding: 10px 30px;
  margin-right: 10px;
  color: var(--white-color);
  background-color: var(--primary-color);
  border-radius: 20px;
  cursor: pointer;
  transition: 0.5s;
}
.btn_container a:hover {
  background-color: transparent;
  box-shadow: 0 0 16px #ffffff30;
}
.section_second {
  width: 100%;
  min-height: 100vh;
  background-color: var(--blue-white-color);
  padding: 20px 10px;
}
.section_second .TopData {
  display: grid;
  text-align: center;
  grid-gap: 10px;
}
.section_second .TopData a {
  padding: 10px 30px;
  background-color: var(--primary-color);
  justify-self: center;
  color: var(--white-color);
  border-radius: 10px;
  transition: 0.5s;
}
.section_second .TopData a:hover {
  background-color: transparent;
  box-shadow: 0 0 16px #00000050;
  color: var(--black-color);
}

.new_content {
  width: 90%;
  margin: auto;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

/* subscribe content  */
.new_container {
  width: 100%;
  height: 80vh;
  background-color: var(--blue-white-color);
  display: flex;
  justify-content: center;
  align-items: center;
}
.inner_container {
  width: 80%;
  height: 80%;
  background-color: var(--primary-color);
  margin: auto;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.container_header h3 {
  font-size: 50px;
  color: var(--white-color);
  text-align: center;
}
.container_inner_content {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  padding: 10px;
}
.container_inner_content input {
  width: 70%;
  height: 50px;
  outline: none;
  border: none;
  border-radius: 10px;
  padding: 10px;
}
.container_inner_content a {
  height: 100%;
  padding: 10px 30px;
  background-color: var(--white-color);
  margin-left: 20px;
  border-radius: 10px;
  color: var(--black-color);
}

.PageTopContent {
  width: 100%;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.top_color {
  width: 100%;
  height: 300px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  border-radius: 0 0 50px 50px;
  background: linear-gradient(36deg, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.9)),
    url(/https//thejaved.github.io/reactwebsite01/static/media/hero_bg.eaf89d00.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  z-index: -1;
}
.PageTopContent h2 {
  color: var(--white-color);
}

.Services_Content {
  text-align: center;
  margin-top: 20px;
}
.image_in_services img {
  margin-top: 20px;
}

.Footer {
  width: 100%;
  height: var(--navbar-height);
  background-color: var(--gry-color);
  display: flex;
  align-items: center;
  justify-content: center;
}
.Footer h3 {
  color: var(--white-color);
}
.Footer a {
  color: var(--primary-color);
}

